























































































































































































import Vue from 'vue';
import Auth from '@/libs/auth';
import { MemberGroupModel, MemberGroupPreviewModel, MemberModel } from '@/apis/models/MemberGroupModel';
import MemberGroupApi from '@/apis/MemberGroupApi';
import BackButton from '@/components/BackButton.vue';

enum MemberRole{
  STUDENT = 'STUDENT',
  TEACHER = 'TEACHER',
  STUDENT_DUPLICATED = 'STUDENT_DUPLICATED',
  STUDENT_EXEMPTED = 'STUDENT_EXEMPTED'
}

export default Vue.extend({
  components: {
    BackButton,
  },
  data() {
    return {
      MemberRole,
      memberGroup: null as MemberGroupModel | null,
      memberGroupFormUrl: 'https://liff.line.me/1656074648-82kPY529#/MemberGroupForm',
      copyMessageDisplay: false,
      copyMessageTimeoutId: 0,
    };
  },
  computed: {
    isLoading(): boolean {
      return this.$store.state.isLoading;
    },
    workspaceId(): string {
      return this.$store.state.currentWorkspace.uuid;
    },
    memberGroupId(): string {
      return this.$store.state.currentMemberGroup.uuid;
    },
    currentMemberGroup(): MemberGroupPreviewModel {
      return this.$store.state.currentMemberGroup;
    },
    confirmedMembers(): number {
      let num = 0;
      if (this.memberGroup) {
        this.memberGroup.members.forEach((item) => {
          if (item.confirmed) {
            num += 1;
          }
        });
      }
      return num;
    },
    notConfirmedMembers(): number {
      let num = 0;
      if (this.memberGroup) {
        num = this.memberGroup.members.length - this.confirmedMembers;
      }
      return num;
    },
    editPermission(): boolean {
      return this.$store.state.editPermission;
    },
    memberGroupQueryUrl(): string {
      if (this.memberGroup) {
        return `${this.memberGroupFormUrl}?name=${this.memberGroup.metadata.name}&uniqueNum=${this.memberGroup.uniqueName.substr(-3)}`;
      }
      return '';
    },
    orderedMembers(): MemberModel[] {
      if (this.memberGroup) {
        let confirmedArr = [] as MemberModel[];
        let unConfirmedArr = [] as MemberModel[];
        this.memberGroup.members.forEach((object) => {
          if (object.confirmed) {
            confirmedArr.push(object);
          } else {
            unConfirmedArr.push(object);
          }
        });
        confirmedArr = this.orderArrayEditor(confirmedArr);
        unConfirmedArr = this.orderArrayEditor(unConfirmedArr);

        const arr = [] as MemberModel[];
        confirmedArr.forEach((element) => {
          arr.push(element);
        });
        unConfirmedArr.forEach((element) => {
          arr.push(element);
        });

        if (arr.length === 0) {
          return this.memberGroup.members;
        }
        return arr;
      }
      return [];
    },
    numberOfTeacher(): number {
      let num = 0;
      if (this.memberGroup) {
        this.memberGroup.members.forEach((item) => {
          if (item.role === MemberRole.TEACHER) {
            num += 1;
          }
        });
      }
      return num;
    },
    numberOfStudent(): number {
      let num = 0;
      if (this.memberGroup) {
        this.memberGroup.members.forEach((item) => {
          if (item.role === MemberRole.STUDENT) {
            num += 1;
          }
        });
      }
      return num;
    },
    numberOfRepeatStudent(): number {
      let num = 0;
      if (this.memberGroup) {
        this.memberGroup.members.forEach((item) => {
          if (item.role === MemberRole.STUDENT_DUPLICATED) {
            num += 1;
          }
        });
      }
      return num;
    },
    numberOfExemptedStudent(): number {
      let num = 0;
      if (this.memberGroup) {
        this.memberGroup.members.forEach((item) => {
          if (item.role === MemberRole.STUDENT_EXEMPTED) {
            num += 1;
          }
        });
      }
      return num;
    },
  },
  async mounted() {
    Auth.loginCheck();
    this.$store.commit('updateLoading', true);
    try {
      this.memberGroup = await MemberGroupApi.getMemberGroup(this.memberGroupId);
    } finally {
      this.$store.commit('updateLoading', false);
    }
  },
  methods: {
    toMemberGroupMaker() {
      this.$router.push('/manager/membergroup-maker');
    },
    copy() {
      const content = `您的班級名稱為"${this.memberGroup?.uniqueName}"，請於互動貼圖頻道填入您的班級名稱，或是點擊以下連結將自動加入班級。\n${this.memberGroupQueryUrl}`;
      this.$copyText(content).then(() => {
        this.copyMessageDisplay = true;
        this.copyMessageTimeoutId = setTimeout(this.clearCopyMessage, 3700);
        this.$notify({
          type: 'success',
          title: this.$t('已複製班級資訊').toString(),
        });
      }, (e) => {
        console.error('複製失败', e.text);
      });
    },
    clearCopyMessage() {
      this.copyMessageDisplay = false;
    },
    clearCopyMessageTimeout() {
      this.clearCopyMessage();
      clearTimeout(this.copyMessageTimeoutId);
    },
    checkMemberBeDuplicated(item: MemberModel) { // 確認這個人是否有被設為重複對象
      let isDuplicated = false;
      if (this.memberGroup) {
        this.memberGroup.members.forEach((member) => {
          if (!isDuplicated) {
            if (member !== item && member.belongsTo === item.uuid) {
              isDuplicated = true;
            }
          }
        });
      }
      return isDuplicated;
    },
    belongsToMemberName(uuid: string) {
      let result = '';
      if (this.memberGroup) {
        this.memberGroup.members.forEach((member) => {
          if (member.uuid === uuid) {
            result = member.name;
          }
        });
      }
      return result;
    },
    orderArrayEditor(array: MemberModel[]) {
      const tempArr = [] as MemberModel[];
      const teacherArr = array.filter((object) => object.role === MemberRole.TEACHER);
      const studentArr = array.filter((object) => object.role === MemberRole.STUDENT);
      const duplicatedStudentArr = array.filter((object) => object.role === MemberRole.STUDENT_DUPLICATED);
      const exemptedStudentArr = array.filter((object) => object.role === MemberRole.STUDENT_EXEMPTED);

      const collator = new Intl.Collator('en', { numeric: true, sensitivity: 'base' });
      const newTeacherArr = teacherArr.sort((a, b) => collator.compare(a.name.toUpperCase().trim(), b.name.toUpperCase().trim()));
      const newStudentArr = studentArr.sort((a, b) => collator.compare(a.name.toUpperCase().trim(), b.name.toUpperCase().trim()));
      const newDuplicatedStudentArr = duplicatedStudentArr.sort((a, b) => collator.compare(a.name.toUpperCase().trim(), b.name.toUpperCase().trim()));
      const newExemptedStudentArr = exemptedStudentArr.sort((a, b) => collator.compare(a.name.toUpperCase().trim(), b.name.toUpperCase().trim()));

      newTeacherArr.forEach((element) => {
        tempArr.push(element);
      });
      newExemptedStudentArr.forEach((element) => {
        tempArr.push(element);
      });
      newStudentArr.forEach((element) => {
        tempArr.push(element);
      });
      newDuplicatedStudentArr.forEach((element) => {
        tempArr.push(element);
      });
      return tempArr;
    },
  },
});
