/* eslint-disable max-classes-per-file */
import { WorkspaceObjectModel } from './WorkspaceObjectModel';

export interface MemberModel {
  openEdit: boolean;
  confirmed: boolean;
  role: string;
  uuid: string;
  name: string;
  businessDefinedId: string;
  belongsTo: string;
}

export class Member implements MemberModel {
  uuid: string;

  name: string;

  businessDefinedId: string;

  openEdit = false;

  confirmed: boolean;

  role: string;

  belongsTo: string;

  constructor(uuid: string, name: string, businessDefinedId: string, confirmed: boolean, role: string, belongsTo: string) {
    this.uuid = uuid;
    this.name = name;
    this.businessDefinedId = businessDefinedId;
    this.confirmed = confirmed;
    this.role = role;
    if (!belongsTo) {
      this.belongsTo = uuid;
    } else {
      this.belongsTo = belongsTo;
    }
  }
}

export class UpdateMemberPayload {
  confirmed: boolean;

  constructor(confirmed: boolean) {
    this.confirmed = confirmed;
  }
}

export class UpdateMemberRolePayload {
  role: string;

  constructor(role: string) {
    this.role = role;
  }
}

export class UpdateMemberGroupPayload {
  name: string;

  constructor(name: string) {
    this.name = name;
  }
}

export class UpdateAutoConfirmPayload {
  autoConfirm: boolean;

  constructor(autoConfirm: boolean) {
    this.autoConfirm = autoConfirm;
  }
}

export interface MemberGroupModel extends WorkspaceObjectModel {
  uniqueName: string;
  members: MemberModel[];
  autoConfirm: boolean;
}

export interface MemberGroupPreviewModel extends WorkspaceObjectModel {
  uniqueName: string;
  memberCount: number;
  previewImageUrl: string;
  autoConfirm: boolean;
}

export interface BelongsToMembersModel extends MemberModel {
  beDuplicatedStudent: MemberModel;
  duplicatedStudent: MemberModel;
}

export interface MemberGroupSentCountModel {
  detail: Map<string, number>;
  totalCount: number;
}

export interface MemberRoleCount {
  totalCount: number;
  confirmedCount: number;
  unconfirmedCount: number;
  sentCount: number;
  unsentCount: number;
  chargedCount: number;
  studentCount: number;
  teacherCount: number;
  repeatStudentCount: number;
  exemptedStudentCount: number;
}
